.projects {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    --project-width: 100vw;
    --project-peek: 0;
    @media ( min-width: 1000px ) {
        --project-width: 800px;
        --project-peek: 400px;
    }
    --project-offset: calc(
        ( ( 100vw - var( --project-width ) ) / 2 ) -
        ( var( --project-peek ) - var( --project-width ) )
    );
    &__button {
        position: absolute;
        z-index: 100;
        top: 2rem;
        transform: translateX(-50%);
        &--exit {
            left: 50%;
        }
        &--prev {
            left: 25%;
        }
        &--next {
            left: 75%;
        }
    }
    &__project {
        width: var(--project-width);
        height: 100%;
        position: absolute;
        left: calc( ( 100vw - var( --project-width ) ) / 2 );
    }
    &__link {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: block;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}