.project {
    display: flex;
    flex-direction: column;
    height: 100%;
    &__header {
        text-align: center;
        z-index: 50;
        h1 {
            margin-top: 120px;
            margin-bottom: .5em;        
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 100%;
            width: 100%;
            height: 1.5em;
            background: linear-gradient( to bottom, var( --background ) 0%, transparent 100% );
        }
    }
    &__body {
        img {
            width: 100%;
        }
        overflow-x: hidden;
        overflow-y: scroll;
        padding-top: 1.5em;
        padding-bottom: 10em;
    }
}